const val discountCodeCookie = "discountCode"
const val discountValueCookie = "discountValue"

private suspend fun getAvailableChips(): String {
    return window.api.get("chips").decodeToString()
}

private suspend fun getUserChips(id: String): String {
    return window.api.get("orders?id=$id").decodeToString()
}

private suspend fun applyCoupon(couponCode: String): CouponData {
    val result = window.api.get("validateCoupon?code=$couponCode").decodeToString()
    return Json.decodeFromString(result)
}

suspend fun submit(
    orders: Map<ChipType, Int>,
    previous: Map<ChipType, Int>,
    userId: String,
    couponCode: String?
): UpdateResult {
    val difference = orders.mapValues { (key, value) -> value - (previous[key] ?: 0) }.filterValues { it != 0 }
    val result = window.api.post(
        "updateOrders?id=$userId${if (couponCode != null) "&coupon=$couponCode" else ""}",
        body = Json.encodeToString(difference.toSerializable()).encodeToByteArray()
    ).decodeToString()
    return result.let { Json.decodeFromString(it) }
}

@Composable
fun ChipForm(id: String, coroutineScope: CoroutineScope, showError: (String?) -> Unit) {

    val chipOrders = mutableStateMapOf<ChipType, Int>()
    val previousOrders = mutableStateMapOf<ChipType, Int>()
    val chips = mutableStateListOf<ChipType>()
    var couponFieldText by remember { mutableStateOf("") }
    var couponCode by remember { mutableStateOf("") }
    var couponDiscount by remember { mutableStateOf(0f) }
    var couponCodeInvalid by remember { mutableStateOf(false) }

    LaunchedEffect(Unit) {
        chips.addAll(Json.decodeFromString<List<ChipType>>(getAvailableChips()))
        chipOrders.putAll(
            Json.decodeFromString<Map<String, Int>>(getUserChips(id))
                .mapKeys { (key, _) -> chips.find { it.id == key }!! })
        previousOrders.putAll(chipOrders)
        val cookies = document.cookie.split(";").map(String::trim).filter(String::isNotBlank)
            .associate { it.split("=").let { (first, second) -> first to second } }
        cookies[discountCodeCookie]?.let { couponCode = it; couponFieldText = it }
        cookies[discountValueCookie]?.let { couponDiscount = it.toFloatOrNull() ?: 0f }
    }


    Form(attrs = listOf(FormStyle).toAttrs {
        onSubmit { evt ->
            evt.preventDefault()
            coroutineScope.launch {
                showError(null)
                val submitResult = submit(chipOrders, previousOrders, id, couponCode.takeIf(String::isNotBlank))
                if (submitResult.success) {
                    previousOrders.apply {
                        clear()
                        putAll(chipOrders)
                    }
                } else {
                    showError(submitResult.message ?: "")
                }
            }
        }
    }) {
        chips.forEach { chipType ->
            ChipRow(chipType, couponDiscount, chipOrders[chipType] ?: 0) { chipOrders[chipType] = it?.toInt() ?: 0 }
        }
        Row(verticalAlignment = Alignment.CenterVertically) {
            TextInput(
                attrs = InputStyle.toModifier(InvalidInput.takeIf { couponCodeInvalid }).toAttrs {
                    name("coupon_field")
                    value(couponFieldText)
                    placeholder("Coupon code")
                    onInput {
                        couponFieldText = it.value
                        couponCodeInvalid = false
                    }
                })
            Button(
                modifier = ChipMarKtButtonStyle.toModifier().margin(left = 5.pt),
                onClick = {
                    coroutineScope.launch {
                        if (couponFieldText.isBlank()) {
                            couponCode = ""
                            couponDiscount = 0f
                            document.cookie = "discountCode=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;"
                            return@launch
                        }
                        showError(null);
                        val (valid, discount) = applyCoupon(couponFieldText)
                        if (valid) {
                            couponCode = couponFieldText
                            couponDiscount = discount
                            document.cookie = "$discountCodeCookie=$couponCode; path=/;"
                            document.cookie = "$discountValueCookie=$discount; path=/;"
                        } else {
                            showError("Not a valid coupon!")
                        }
                        couponCodeInvalid = !valid
                    }
                }) {
                SpanText("Apply code")
            }
            SpanText("Current code: \"$couponCode\"", variant = CouponCodeCurrentVariant)
            Spacer()
            SpanText(
                "Total Price: $${
                    chipOrders.map { (key, value) -> value * key.basePrice }.sum()
                        .let { round(it * (1 - couponDiscount) * 100f) / 100f }
                }",
                modifier = Modifier.margin(right = 10.pt)
            )
        }
        Row(modifier = Modifier.margin(top = 10.pt), horizontalArrangement = Arrangement.Center) {
            Button(ButtonStyle.toModifier().then(ChipMarKtButtonStyle.toModifier()).toAttrs {
                type(ButtonType.Submit)
                value("Update Order")
            }) {
                SpanText("Update Order")
            }
        }
    }
}

@Composable
fun ElementScope<HTMLFormElement>.ChipRow(
    chipType: ChipType,
    discount: Float,
    value: Number,
    onChange: (Number?) -> Unit
) {
    val (name, info, basePrice) = chipType
    Row(
        verticalAlignment = Alignment.CenterVertically,
        modifier = ChipRowStyle.toModifier()
    ) {
        SpanText(name, modifier = Modifier.margin(right = 4.pt))
        info.takeIf { it.isNotBlank() }
            ?.let { infoText -> SpanText(infoText, modifier = Modifier.fontStyle(FontStyle.Italic)) }
        Spacer()
        NumberInput(
            value = value,
            min = 0,
            attrs = InputStyle.toAttrs(AmountInputStyle) {
                placeholder("amount")
                name("amount_$name")
                value(value)

                fun Number.isNan() = toFloat().isNaN()

                onInput {
                    if (it.value == null || it.data == null && it.value!!.isNan()) {
                        onChange(0)
                    } else if (!it.value!!.isNan() && it.value!!.toFloat() <= Int.MAX_VALUE) {
                        onChange(it.value)
                    }
                }
            }
        )
        SpanText(
            "  $${basePrice.let { round(it * (1 - discount) * 100f) / 100f }}",
            modifier = Modifier.margin(right = 10.pt)
        )
    }
}