package de.mr_pine.chipmarkt.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.browser.api
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Spacer
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxSize
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.maxWidth
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import de.mr_pine.chipmarkt.widgets.*
import kotlinx.browser.window
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.vw


@Page
@Composable
fun HomePage() {
    var id by remember { mutableStateOf("") }
    var ready by remember { mutableStateOf(false) }
    val coroutineScope = rememberCoroutineScope()

    var errorMessage: String? by remember { mutableStateOf(null) }
    var flag: String? by remember { mutableStateOf(null) }

    LaunchedEffect(Unit) {
        id = window.localStorage.getItem("id") ?: run {
            window.api.get("id").decodeToString().also {
                window.localStorage.setItem("id", it)
            }
        }

        ready = true
    }

    Column(
        modifier = Modifier.fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.Top,
    ) {
        if (!ready) {
            Box(Modifier.fillMaxWidth().margin(top = 2.cssRem), contentAlignment = Alignment.TopCenter) {
                LoadingSpinner()
            }
            return@Column
        }

        TitleBar(coroutineScope, id, { flag = it }) { errorMessage = it }

        if (errorMessage != null) {
            ErrorMessage(errorMessage!!) { errorMessage = null }
        }

        Column(Modifier.fillMaxSize(), horizontalAlignment = Alignment.CenterHorizontally) {
            if (flag == null) {
                Column(
                    Modifier.fillMaxWidth().maxWidth(80.vw).align(Alignment.CenterHorizontally),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    ChipForm(id = id, coroutineScope = coroutineScope) { errorMessage = it }
                }
            } else {
                Success(flag ?: "")
            }
        }

        Spacer()
        SpanText("Powered by Kotlin/Native, Kotlin/JVM and Kotlin/JS", modifier = PoweredByStyle.toModifier())
    }

    KodeeHelp(coroutineScope)
}