package de.mr_pine.chipmarkt.widgets

import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.css.CSSAnimation
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.StyleModifier
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.animation.Keyframes
import com.varabyte.kobweb.silk.components.layout.SurfaceStyle
import com.varabyte.kobweb.silk.components.style.*
import com.varabyte.kobweb.silk.components.style.common.ariaDisabled
import com.varabyte.kobweb.silk.components.text.SpanTextStyle
import com.varabyte.kobweb.silk.theme.shapes.Rect
import com.varabyte.kobweb.silk.theme.shapes.clip
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.css.keywords.auto
import org.jetbrains.compose.web.css.Color as CSSColor

private val INTERACT_COLOR = Color.rgb(0x00, 0x70, 0xf3)
private val CONTENT_COLOR = Color.rgb(0xf4f4f4)
val BACKGROUND_DARK = Color.rgb(0x27282c)
val BACKGROUND_DARKER = Color.rgb(0x1b1c1f)
private val BACKGROUND_LIGHT = Color.rgb(0x3d3d41)
private val KOTLIN_COLOR_RED = Color.rgb(0xE44857)
private val KOTLIN_COLOR_LIGHT = Color.rgb(0xC711E1)
private val KOTLIN_COLOR_DARK = Color.rgb(0x7F52FF)
private val KOTLIN_COLOR_INTERACT = Color.rgb(0x7e45fa)

val FormStyle by ComponentStyle.base {
    Modifier
        .width(85.percent)
        .margin(top = 10.pt)
        .transition(*CSSTransition.group(listOf("color", "border-color"), 0.15.s, TransitionTimingFunction.Ease))
        .textDecorationLine(TextDecorationLine.None)
}

val ChipMarKtButtonStyle by ComponentStyle {
    base {
        Modifier
            .backgroundColor(KOTLIN_COLOR_INTERACT)
            .color(Color.rgb(0xf4f4f4))
            .borderWidth(1.pt)
            .borderStyle(LineStyle.Solid)
            .borderColor(CSSColor.transparent)
            .clip(Rect(1000.pt))
    }
    hover {
        Modifier
            .backgroundColor(KOTLIN_COLOR_DARK)
            .borderColor(KOTLIN_COLOR_INTERACT)
            .borderRadius(1000.pt)
    }

    (hover + not(ariaDisabled)) {
        Modifier.backgroundColor(KOTLIN_COLOR_DARK)
    }
}

val InputStyle by ComponentStyle {
    base {
        Modifier
            .backgroundColor(BACKGROUND_LIGHT)
            .color(CONTENT_COLOR)
            .clip(Rect(1000.pt))
            .borderRadius(1000.pt)
            .borderColor(CONTENT_COLOR.copy(alpha = 128))
            .borderStyle(LineStyle.Solid)
            .minHeight(25.pt)
            .then(StyleModifier {
                property("appearance", "textfield")
                property("-moz-appearance", "textfield")
            })
            .padding(left = 5.pt)
    }

    val noSpinnerButtons = {
        StyleModifier {
            property("-webkit-appearance", "none")
        }.margin(0.px)
    }

    CssRule.OfPseudoElement(this, "-webkit-inner-spin-button")(noSpinnerButtons)
    CssRule.OfPseudoElement(this, "-webkit-outer-spin-button")(noSpinnerButtons)

    focus {
        Modifier.borderColor(KOTLIN_COLOR_DARK)
    }

    placeholderShown
}

val CouponCodeCurrentVariant by SpanTextStyle.addVariant {
    base {
        Modifier.fontStyle(FontStyle.Italic).color(CONTENT_COLOR.copy(alpha = 128)).margin(left = 10.pt)
    }
}

val InvalidInput by InputStyle.addVariant {
    base {
        Modifier.borderColor(KOTLIN_COLOR_RED)
    }
}

val AmountInputStyle by InputStyle.addVariant {
    base {
        Modifier
            .width(auto)
            .margin(topBottom = 2.5f.pt)
            .backgroundColor(BACKGROUND_DARK)
            .borderWidth(0.7f.pt)
    }

    placeholderShown {
        Modifier.fontStyle(FontStyle.Italic)
    }
}

val ChipRowStyle by ComponentStyle {
    base {
        Modifier
            .clip(Rect(1000.pt))
            .backgroundColor(BACKGROUND_LIGHT)
            .padding(5.pt)
            .padding(left = 10.pt)
            .margin(topBottom = 5.pt)
    }
}

val ChipMarKtSurfaceVariant by SurfaceStyle.addVariant {
    base {
        Modifier
            .backgroundColor(BACKGROUND_DARK)
            .color(CONTENT_COLOR)
    }
}

val Shimmer by Keyframes {
    from {
        Modifier.backgroundPosition(BackgroundPosition.of(CSSPosition((-150).px, 0.px)))
    }
    to {
        Modifier.backgroundPosition(BackgroundPosition.of(CSSPosition(150.px, 0.px)))
    }
}

val HighlightedChipMarKtButtonVariant by ChipMarKtButtonStyle.addVariant {
    hover {
        Modifier
            .backgroundImage(
                linearGradient(angle = 45.deg) {
                    add(KOTLIN_COLOR_DARK, 22.585878.percent)
                    add(KOTLIN_COLOR_LIGHT, 45.percent)
                    add(KOTLIN_COLOR_DARK, 72.41412f.percent)
                }
            )
            .backgroundRepeat(BackgroundRepeat.NoRepeat)
            .animation(
                CSSAnimation(
                    name = "shimmer",
                    duration = 3.s,
                    iterationCount = AnimationIterationCount.Infinite,
                    timingFunction = AnimationTimingFunction.Linear
                )
            )
    }
}

val ChatBoxStyle by ComponentStyle {
    base {
        Modifier
            .fillMaxWidth()
            .clip(Rect(cornerRadius = 15.pt))
            .backgroundColor(BACKGROUND_DARKER)
    }
}

val ChatBoxInputRow by ComponentStyle {
    base {
        Modifier
            .fillMaxWidth()
            .backgroundColor(BACKGROUND_DARKER)
            .padding(7.pt)
    }
}

val ChatInputVariant by InputStyle.addVariant {
    base {
        Modifier.backgroundColor(BACKGROUND_LIGHT)
            .clip(Rect(5.pt))
            .borderRadius(5.pt)
            .fontFamily("inherit")
            .fillMaxWidth()
            .padding(5.pt)
    }
}

val ChatMessageStyle by ComponentStyle {
    base {
        Modifier.padding(3.pt)
            .backgroundColor(BACKGROUND_LIGHT)
            .overflowWrap(OverflowWrap.Anywhere)
            .margin(topBottom = 3.pt, leftRight = 1.5f.pt)
    }
}

val ChatMessageKodeeVariant by ChatMessageStyle.addVariant {
    base {
        Modifier
            .borderRadius(topLeft = 0.pt, 5.pt, 5.pt, 5.pt)
    }
}

val ChatMessageUserVariant by ChatMessageStyle.addVariant {
    base {
        Modifier
            .borderRadius(5.pt, topRight = 0.pt, 5.pt, 5.pt)
    }
}

val ErrorMessageBox by ComponentStyle {
    base {
        Modifier
            .borderRadius(15.pt)
            .borderColor(KOTLIN_COLOR_RED)
            .borderWidth(2.pt)
            .borderStyle(LineStyle.Solid)
            .padding(leftRight = 10.pt, topBottom = 5.pt)
            .margin(15.pt)
    }
}

val ErrorCloseButton by ComponentStyle {
    base {
        Modifier
            .backgroundColor(CSSColor.transparent)
            .color(CONTENT_COLOR)
            .padding(2.pt)
    }

    (hover + not(ariaDisabled)) {
        Modifier.backgroundColor(CSSColor.transparent)
            .color(KOTLIN_COLOR_RED)
    }
}

val PoweredByStyle by ComponentStyle {
    base {
        Modifier
            .color(CONTENT_COLOR.copy(alpha = 128))
            .margin(bottom = 3.pt)
    }
}