package de.mr_pine.chipmarkt

const val ANSI_RESET = "\u001B[0m"
const val ANSI_BLACK = "\u001B[30m"
const val ANSI_RED = "\u001B[31m"
const val ANSI_GREEN = "\u001B[32m"
const val ANSI_YELLOW = "\u001B[33m"
const val ANSI_BLUE = "\u001B[34m"
const val ANSI_PURPLE = "\u001B[35m"
const val ANSI_CYAN = "\u001B[36m"
const val ANSI_WHITE = "\u001B[37m"

expect val PLATFORM_COLOR: String
expect val PLATFORM_IDENTIFIER: String
val platformTag
    get() = "[${PLATFORM_IDENTIFIER.colored(PLATFORM_COLOR)}]\t "

fun String.colored(colorCode: String) = colorCode + this + ANSI_RESET

fun log(message: Any?) {
    println(platformTag + message)
}
