package de.mr_pine.chipmarkt.widgets

import androidx.compose.runtime.*
import com.varabyte.kobweb.browser.api
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import de.mr_pine.chipmarkt.util.ConversationElement
import de.mr_pine.chipmarkt.util.ConversationRole
import kotlinx.browser.window
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.launch
import org.jetbrains.compose.web.attributes.name
import org.jetbrains.compose.web.attributes.onSubmit
import org.jetbrains.compose.web.attributes.placeholder
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.pt
import org.jetbrains.compose.web.dom.Form
import org.jetbrains.compose.web.dom.TextArea

@Composable
fun KodeeHelp(coroutineScope: CoroutineScope) {

    var kodeeVisible by remember { mutableStateOf(true) }

    val kodeeImage = remember {
        listOf("", "greet", "naughty", "wave").random().let { "kodee/Kodee_$it".removeSuffix("_") + ".svg" }
    }

    val conversation = mutableStateListOf(
        ConversationElement("Hi, I'm Kodee, how can I help you today?", ConversationRole.KODEE)
    )

    var chatInput by remember { mutableStateOf("") }

    fun submitQuestion() = coroutineScope.launch {
        val question = chatInput
        val answer = window.api.tryGet("kodee?question=$question")?.decodeToString() ?: return@launch

        conversation.add(ConversationElement(question, ConversationRole.USER))
        conversation.addAll(answer.split("\n").map {
            ConversationElement(it, ConversationRole.KODEE)
        })
        chatInput = ""
    }

    Column(
        modifier = Modifier
            .position(Position.Fixed)
            .right(15.pt)
            .bottom(15.pt)
            .width(300.pt)
    ) {
        if(kodeeVisible) Column(modifier = ChatBoxStyle.toModifier()) {
            Column(
                modifier = Modifier
                    .maxHeight(400.pt)
                    .overflow(Overflow.Scroll)
                    .fillMaxWidth()
                    .padding(5.pt)
                    .margin(top = 3.pt)
            ) {
                for (conversationElement in conversation) {
                    val isKodee = conversationElement.role == ConversationRole.KODEE
                    Row(
                        modifier = Modifier.fillMaxWidth(),
                        horizontalArrangement = if (isKodee) Arrangement.Start else Arrangement.End
                    ) {
                        Row(
                            modifier = ChatMessageStyle.toModifier(if (isKodee) ChatMessageKodeeVariant else ChatMessageUserVariant),
                            verticalAlignment = Alignment.CenterVertically
                        ) {
                            if (isKodee) {
                                Image(
                                    "kodee/Kodee_icon_small.svg",
                                    modifier = Modifier.width(15.pt).margin(right = 5.pt)
                                )
                            }
                            SpanText(conversationElement.content, modifier = Modifier.maxWidth(200.pt))
                        }
                    }
                }
            }

            Form(attrs = Modifier.fillMaxWidth().toAttrs {
                onSubmit {
                    it.preventDefault()
                    submitQuestion()
                }
            }) {
                Row(modifier = ChatBoxInputRow.toModifier()) {
                    TextArea(chatInput, InputStyle.toModifier(ChatInputVariant).onKeyDown { event ->
                        if (event.key == "Enter" && !event.shiftKey) {
                            event.preventDefault()
                            submitQuestion()
                        }
                    }.toAttrs {
                        name("question")
                        placeholder("Ask me something!")
                        onInput {
                            chatInput = it.value
                        }
                    })
                }
            }
        }


        Row(modifier = Modifier.fillMaxWidth().margin(top = 15.pt), horizontalArrangement = Arrangement.End) {
            Image(kodeeImage, modifier = Modifier.width(50.pt).onClick { kodeeVisible = !kodeeVisible })
        }
    }
}