package de.mr_pine.chipmarkt.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.browser.api
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import de.mr_pine.chipmarkt.log
import kotlinx.browser.window
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.launch
import org.jetbrains.compose.web.css.*


val TitleStyle by ComponentStyle.base {
    Modifier
        .margin(top = 0.4.em)
        .padding(left = 0.6.em, right = 0.6.em, bottom = 0.6.em)
        .fontWeight(FontWeight.Bold)
}

@Composable
fun ColumnScope.TitleBar(coroutineScope: CoroutineScope, id: String, showFlag: (String) -> Unit, showError: (String) -> Unit) {
    val titleHeight = 3f.cssRem
    Row(modifier = TitleStyle.toModifier().fillMaxWidth().maxHeight(titleHeight + 0.5f.cssRem).align(Alignment.Start).boxShadow(inset = true, offsetY = (-1).px, color = Color.rgba(1f, 1f, 1f, .3f))) {
        SpanText("ChipMar", modifier = Modifier.fontSize(titleHeight))
        Image("/kt.svg", modifier = Modifier.height(100.percent).margin(left = 3.pt))
        Spacer()
        Column(modifier = Modifier.fillMaxHeight(), verticalArrangement = Arrangement.Center) {
            Button(
                modifier = ChipMarKtButtonStyle.toModifier(HighlightedChipMarKtButtonVariant),
                onClick = {
                coroutineScope.launch {
                    val flag = getFlag(id)
                    log(flag ?: "You are not a premium customer")
                    if(flag == null) {
                        showError("You are not a premium customer. Our premium customer program rewards customers that place orders of at least $ 1,000,000,000")
                    } else {
                        showFlag(flag)
                    }
                }
            }) {
                SpanText("Premium Area")
            }
        }
    }
}

suspend fun getFlag(id: String): String? = window.api.get("flag?id=$id")!!.decodeToString().takeIf { it != "null" }