import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { de.mr_pine.chipmarkt.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.TitleStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.FormStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ChipMarKtButtonStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.InputStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ChipRowStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ChatBoxStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ChatBoxInputRow)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ChatMessageStyle)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ErrorMessageBox)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.ErrorCloseButton)
        ctx.theme.registerComponentStyle(de.mr_pine.chipmarkt.widgets.PoweredByStyle)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.CouponCodeCurrentVariant)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.InvalidInput)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.AmountInputStyle)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.ChipMarKtSurfaceVariant)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.HighlightedChipMarKtButtonVariant)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.ChatInputVariant)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.ChatMessageKodeeVariant)
        ctx.theme.registerComponentVariants(de.mr_pine.chipmarkt.widgets.ChatMessageUserVariant)
        ctx.stylesheet.registerKeyframes(de.mr_pine.chipmarkt.widgets.Shimmer)
        de.mr_pine.chipmarkt.initSiteStyles(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "ChipMarKt")
        ) { de.mr_pine.chipmarkt.ChipMarKt {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
