import org.jetbrains.compose.web.css.em
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.pt

@Composable
fun ColumnScope.Success(flag: String) {
    Image(
        "https://media.tenor.com/2pXQnDFV-CAAAAAC/doctor-evil-austin-powers.gif",
        modifier = Modifier.margin(topBottom = 15.pt)
    )

    Row(Modifier.fillMaxWidth(80.percent), horizontalArrangement = Arrangement.Center) {
        Image("kodee/Kodee_flag.svg", modifier = Modifier.maxWidth(150.pt).margin(right = 10.pt))
        Column(horizontalAlignment = Alignment.CenterHorizontally) {
            SpanText("Congrats! \uD83C\uDF89", Modifier.fontWeight(FontWeight.Bold).fontSize(2.5.em))
            SpanText("Thank you for paying us a lot of Money", Modifier.fontSize(1.5f.em))
            SpanText("Here's your Flag: $flag", Modifier.fontSize(1.5f.em).margin(top = 2.pt))
            SpanText(
                "And try writing Kotlin some time. It's really fun",
                Modifier.margin(top = 5.pt).fontStyle(FontStyle.Italic)
            )
        }
    }
}