package de.mr_pine.chipmarkt.widgets

import androidx.compose.runtime.Composable
import androidx.compose.web.events.SyntheticMouseEvent
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.icons.mdi.MdiClose
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.css.pt

@Composable
fun ErrorMessage(message: String, hide: (SyntheticMouseEvent) -> Unit) {
    Row(
        horizontalArrangement = Arrangement.Center,
        verticalAlignment = Alignment.CenterVertically,
        modifier = ErrorMessageBox.toModifier()
    ) {
        Image("kodee/Kodee_Error.svg", modifier = Modifier.height(35.pt).margin(right = 15.pt))
        SpanText("Oh no! Something went wrong. Here's a (maybe) helpful little message for you: $message")
        Button(onClick = hide, modifier = ErrorCloseButton.toModifier()) {
            MdiClose()
        }
    }
}